    color: #1e1e1e;
  }
  .tabs-content {
      margin-left: 30px;
    text-align: left;
    display: inline-block;
    transition: all 0.3s;
  }
  .tabs-content img {
      max-width: 100%;
      overflow: hidden;
  }
  .tabs-content h4 {
      font-size: 20px;
      font-weight: 700;
      letter-spacing: 0.25px;
      margin-bottom: 15px;
      margin-top: 30px;
  }
  .tabs-content p {
    font-size: 14px;
    color: #7a7a7a;
    margin-bottom: 0px;
  }
  
  
  
  /* Contact Information */
  
  .contact-information {
      margin-top: 140px;
  }
  
  .contact-information .contact-item {
      padding: 60px 30px;
      background-color: #f7f7f7;
      text-align: center;
  }
  
  .contact-information .contact-item i {
      color: #a4c639;
      font-size: 48px;
      margin-bottom: 40px;
  }
  
  .contact-information .contact-item h4 {
      font-size: 20px;
      font-weight: 700;
      letter-spacing: 0.25px;
      margin-bottom: 15px;
  }
  
  .contact-information .contact-item p {
      margin-bottom: 20px;
  }
  
  .contact-information .contact-item a {
      font-weight: 600;
      color: #a4c639;
      font-size: 15px;
  }
  
  
  
  
  /* Responsive Style */
  @media (max-width: 768px) {
      .sub-header {
          display: none;
      }
      .Modern-Slider .item h6 {
        margin-bottom: 15px;
        font-size: 18px;
      }
      .Modern-Slider .item h4 {
        margin-bottom: 25px;
        font-size: 28px;
        line-height: 36px;
        letter-spacing: 1px;
      }
      .Modern-Slider .item p {
          max-width: 570px;
          line-height: 25px;
          margin-bottom: 30px;
      }
      .Modern-Slider .NextArrow{
        right:5px;
      }
      .Modern-Slider .PrevArrow {
        left:5px;
      }
      .request-form {
          text-align: center;
      }
      .request-form a.border-button {
          float: none;
          margin-top: 30px;
      }
      .services .service-item {
          margin-bottom: 30px;
      }
      .fun-facts .left-content {
          margin-right: 0px;
          margin-bottom: 30px;
      }
      .more-info .right-content  {
          padding: 30px;
      }
      footer {
          padding: 80px 0px 20px 0px;
      }
      footer .footer-item {
          border-bottom: 1px solid #343434;
          margin-bottom: 30px;
          padding-bottom: 30px;
      }
      footer .last-item {
          border-bottom: none;
      }
      .about-info .right-content {
          margin-right: 0px;
          margin-bottom: 30px;
      }
      .team .team-item {
          margin-bottom: 30px;
      }
      .tabs-content {
          margin-left: 0px;
          margin-top: 30px;
      }
      .contact-item {
          margin-bottom: 30px;
      }
  }
  
  @media (max-width: 992px) {
      .navbar .navbar-brand {
          position: absolute;
          left: 30px;
          top: 10px;
      }
      .navbar .navbar-brand {
          width: auto;
      }
      .navbar:after {
          display: none;
      }
      #navbarResponsive {
          z-index: 99999;
          position: absolute;
          top: 80px;
          left: 0;
          width: 100%;
          text-align: center;
          background-color: #ffffff;
          box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
      }
      .navbar .navbar-nav .nav-item {
          border-bottom: 1px solid #eee;
      }
      .navbar .navbar-nav .nav-item:last-child {
          border-bottom: none;
      }
      .navbar .navbar-nav a.nav-link {
          padding: 15px 0px;
          color: #1e1e1e!important;
      }
      .navbar .navbar-nav .nav-link:hover,
      .navbar .navbar-nav .active>.nav-link,
      .navbar .navbar-nav .nav-link.current,
      .navbar .navbar-nav .nav-link.show,
      .navbar .navbar-nav .show>.nav-link {
          color: #a4c639!important;
          border-bottom: none!important;
      }
  }